import React, { useEffect } from 'react'
import { ProductsList, ProductEditor } from '../../components/admin/products'
import AdminNavigation from '../../components/admin/navigation/AdminNavigation'
import useAdminProductsStore from '../../stores/adminProductsStore'
import '../../components/admin/products/products-admin.scss'

const ProductsManager = () => {
  const { loadProducts, hasUnsavedChanges } = useAdminProductsStore()

  useEffect(() => {
    loadProducts()
  }, [loadProducts])

  // –ü–æ–ø–µ—Ä–µ–¥–∂–µ–Ω–Ω—è –ø—Ä–∏ —Å–ø—Ä–æ–±—ñ –ø–æ–∫–∏–Ω—É—Ç–∏ —Å—Ç–æ—Ä—ñ–Ω–∫—É –∑ –Ω–µ–∑–±–µ—Ä–µ–∂–µ–Ω–∏–º–∏ –∑–º—ñ–Ω–∞–º–∏
  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (hasUnsavedChanges) {
        e.preventDefault()
        e.returnValue = '–£ –≤–∞—Å —î –Ω–µ–∑–±–µ—Ä–µ–∂–µ–Ω—ñ –∑–º—ñ–Ω–∏. –í–∏ –≤–ø–µ–≤–Ω–µ–Ω—ñ, —â–æ —Ö–æ—á–µ—Ç–µ –ø–æ–∫–∏–Ω—É—Ç–∏ —Å—Ç–æ—Ä—ñ–Ω–∫—É?'
      }
    }

    window.addEventListener('beforeunload', handleBeforeUnload)
    return () => window.removeEventListener('beforeunload', handleBeforeUnload)
  }, [hasUnsavedChanges])

  return (
    <div className="products-manager">
      <AdminNavigation pageType="products" />

      <div className="products-manager-header">
        <h1>–£–ø—Ä–∞–≤–ª—ñ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç–∞–º–∏</h1>
        <p>–°—Ç–≤–æ—Ä—é–π—Ç–µ, —Ä–µ–¥–∞–≥—É–π—Ç–µ —Ç–∞ –∫–µ—Ä—É–π—Ç–µ –∫–∞—Ç–∞–ª–æ–≥–æ–º –≥—Ä–∞–Ω—ñ—Ç–Ω–∏—Ö –≤–∏—Ä–æ–±—ñ–≤</p>
        {hasUnsavedChanges && (
          <div style={{
            color: '#f59e0b',
            fontSize: '0.875rem',
            marginTop: '0.5rem',
            fontWeight: '500'
          }}>
            ‚ö†Ô∏è –£ –≤–∞—Å —î –Ω–µ–∑–±–µ—Ä–µ–∂–µ–Ω—ñ –∑–º—ñ–Ω–∏
          </div>
        )}
      </div>

      <div className="products-manager-content">
        <ProductsList />
        <ProductEditor />
      </div>
    </div>
  )
}

export default ProductsManager