import React, { useState, useEffect } from 'react'
import useAdminProductsStore from '../../../stores/adminProductsStore'
import { surfaceFinishTypes, standardSizes } from '../../../constants/productsData'

const ProductEditor = () => {
  const {
    editingProduct,
    isEditModalOpen,
    closeEditModal,
    addProduct,
    updateProduct,
    isLoading,
    error
  } = useAdminProductsStore()

  const [formData, setFormData] = useState({
    name: {
      ua: '',
      en: '',
      de: '',
      pl: ''
    },
    textureId: 'black-001',
    finishType: 'sawn-thermal-top',
    size: 'paver_200x100x50',
    dimensions: { ...standardSizes.paver_200x100x50 },
    price: {
      ua: '',
      en: '',
      de: '',
      pl: ''
    },
    image: '',
    description: {
      ua: '',
      en: '',
      de: '',
      pl: ''
    },
    features: {
      ua: [],
      en: [],
      de: [],
      pl: []
    },
    inStock: true,
    customizable: true
  })

  const [customDimensions, setCustomDimensions] = useState(false)
  const [newFeature, setNewFeature] = useState({
    ua: '', en: '', de: '', pl: ''
  })

  // –ó–∞–ø–æ–≤–Ω–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏ –ø—Ä–∏ —Ä–µ–¥–∞–≥—É–≤–∞–Ω–Ω—ñ
  useEffect(() => {
    if (editingProduct) {
      setFormData({
        name: editingProduct.name || { ua: '', en: '', de: '', pl: '' },
        textureId: editingProduct.textureId || 'black-001',
        finishType: editingProduct.finishType || 'sawn-thermal-top',
        size: editingProduct.size || 'paver_200x100x50',
        dimensions: editingProduct.dimensions || { ...standardSizes.paver_200x100x50 },
        price: editingProduct.price || { ua: '', en: '', de: '', pl: '' },
        image: editingProduct.image || '',
        description: editingProduct.description || { ua: '', en: '', de: '', pl: '' },
        features: editingProduct.features || { ua: [], en: [], de: [], pl: [] },
        inStock: editingProduct.inStock !== undefined ? editingProduct.inStock : true,
        customizable: editingProduct.customizable !== undefined ? editingProduct.customizable : true
      })

      setCustomDimensions(editingProduct.size === 'custom')
    } else {
      // –°–∫–∏–¥–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏ –¥–ª—è –Ω–æ–≤–æ–≥–æ –ø—Ä–æ–¥—É–∫—Ç—É
      setFormData({
        name: { ua: '', en: '', de: '', pl: '' },
        textureId: 'black-001',
        finishType: 'sawn-thermal-top',
        size: 'paver_200x100x50',
        dimensions: { ...standardSizes.paver_200x100x50 },
        price: { ua: '', en: '', de: '', pl: '' },
        image: '',
        description: { ua: '', en: '', de: '', pl: '' },
        features: { ua: [], en: [], de: [], pl: [] },
        inStock: true,
        customizable: true
      })
      setCustomDimensions(false)
    }
  }, [editingProduct])

  // –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —Ä–æ–∑–º—ñ—Ä—É
  useEffect(() => {
    if (formData.size !== 'custom' && standardSizes[formData.size]) {
      setFormData(prev => ({
        ...prev,
        dimensions: { ...standardSizes[formData.size] }
      }))
      setCustomDimensions(false)
    } else if (formData.size === 'custom') {
      setCustomDimensions(true)
    }
  }, [formData.size])

  const handleInputChange = (field, value, lang = null) => {
    if (lang) {
      setFormData(prev => ({
        ...prev,
        [field]: {
          ...prev[field],
          [lang]: value
        }
      }))
    } else {
      setFormData(prev => ({
        ...prev,
        [field]: value
      }))
    }
  }

  const handleDimensionChange = (dimension, value) => {
    const numValue = parseFloat(value) || 0
    setFormData(prev => ({
      ...prev,
      dimensions: {
        ...prev.dimensions,
        [dimension]: numValue
      }
    }))
  }

  const addFeature = () => {
    if (newFeature.ua.trim()) {
      setFormData(prev => ({
        ...prev,
        features: {
          ua: [...prev.features.ua, newFeature.ua.trim()],
          en: [...prev.features.en, newFeature.en.trim() || newFeature.ua.trim()],
          de: [...prev.features.de, newFeature.de.trim() || newFeature.ua.trim()],
          pl: [...prev.features.pl, newFeature.pl.trim() || newFeature.ua.trim()]
        }
      }))
      setNewFeature({ ua: '', en: '', de: '', pl: '' })
    }
  }

  const removeFeature = (index) => {
    setFormData(prev => ({
      ...prev,
      features: {
        ua: prev.features.ua.filter((_, i) => i !== index),
        en: prev.features.en.filter((_, i) => i !== index),
        de: prev.features.de.filter((_, i) => i !== index),
        pl: prev.features.pl.filter((_, i) => i !== index)
      }
    }))
  }

  const handleSubmit = async (e) => {
    e.preventDefault()

    // –í–∞–ª—ñ–¥–∞—Ü—ñ—è
    if (!formData.name.ua.trim()) {
      alert('–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤—É –ø—Ä–æ–¥—É–∫—Ç—É —É–∫—Ä–∞—ó–Ω—Å—å–∫–æ—é')
      return
    }

    if (!formData.price.ua.trim()) {
      alert('–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å —Ü—ñ–Ω—É')
      return
    }

    try {
      let result
      if (editingProduct) {
        result = await updateProduct(editingProduct.id, formData)
      } else {
        result = await addProduct(formData)
      }

      if (result.success) {
        closeEditModal()
      } else {
        alert(`–ü–æ–º–∏–ª–∫–∞: ${result.error}`)
      }
    } catch (error) {
      alert(`–ü–æ–º–∏–ª–∫–∞: ${error.message}`)
    }
  }

  if (!isEditModalOpen) return null

  return (
    <div className="product-editor-overlay">
      <div className="product-editor-modal">
        <div className="modal-header">
          <h2>
            {editingProduct ? '–†–µ–¥–∞–≥—É–≤–∞—Ç–∏ –ø—Ä–æ–¥—É–∫—Ç' : '–î–æ–¥–∞—Ç–∏ –Ω–æ–≤–∏–π –ø—Ä–æ–¥—É–∫—Ç'}
          </h2>
          <button
            onClick={closeEditModal}
            className="close-btn"
            disabled={isLoading}
          >
            ‚úï
          </button>
        </div>

        <form onSubmit={handleSubmit} className="product-form">
          <div className="form-sections">
            {/* –û—Å–Ω–æ–≤–Ω–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è */}
            <div className="form-section">
              <h3>–û—Å–Ω–æ–≤–Ω–∞ —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è</h3>

              <div className="form-group">
                <label>–ù–∞–∑–≤–∞ –ø—Ä–æ–¥—É–∫—Ç—É *</label>
                <div className="lang-inputs">
                  <input
                    type="text"
                    placeholder="–£–∫—Ä–∞—ó–Ω—Å—å–∫–∞"
                    value={formData.name.ua}
                    onChange={(e) => handleInputChange('name', e.target.value, 'ua')}
                    required
                  />
                  <input
                    type="text"
                    placeholder="English"
                    value={formData.name.en}
                    onChange={(e) => handleInputChange('name', e.target.value, 'en')}
                  />
                  <input
                    type="text"
                    placeholder="Deutsch"
                    value={formData.name.de}
                    onChange={(e) => handleInputChange('name', e.target.value, 'de')}
                  />
                  <input
                    type="text"
                    placeholder="Polski"
                    value={formData.name.pl}
                    onChange={(e) => handleInputChange('name', e.target.value, 'pl')}
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>–¢–∏–ø —Ç–µ–∫—Å—Ç—É—Ä–∏</label>
                  <select
                    value={formData.textureId}
                    onChange={(e) => handleInputChange('textureId', e.target.value)}
                  >
                    <option value="black-001">–ß–æ—Ä–Ω–∏–π –≥—Ä–∞–Ω—ñ—Ç (Black-001)</option>
                    <option value="green-001">–ó–µ–ª–µ–Ω–∏–π –≥—Ä–∞–Ω—ñ—Ç (Green-001)</option>
                    <option value="green-002">–ó–µ–ª–µ–Ω–∏–π –≥—Ä–∞–Ω—ñ—Ç (Green-002)</option>
                    <option value="red-brown-002">–ß–µ—Ä–≤–æ–Ω–æ-–∫–æ—Ä–∏—á–Ω–µ–≤–∏–π (Red-Brown-002)</option>
                    <option value="red-brown-003">–ß–µ—Ä–≤–æ–Ω–æ-–∫–æ—Ä–∏—á–Ω–µ–≤–∏–π (Red-Brown-003)</option>
                    <option value="red-brown-008">–ß–µ—Ä–≤–æ–Ω–æ-–∫–æ—Ä–∏—á–Ω–µ–≤–∏–π (Red-Brown-008)</option>
                    <option value="gray-001">–°—ñ—Ä–∏–π –≥—Ä–∞–Ω—ñ—Ç (Gray-001)</option>
                    <option value="labradorite-001">–õ–∞–±—Ä–∞–¥–æ—Ä–∏—Ç (Labradorite-001)</option>
                  </select>
                </div>

                <div className="form-group">
                  <label>–¢–∏–ø –æ–±—Ä–æ–±–∫–∏</label>
                  <select
                    value={formData.finishType}
                    onChange={(e) => handleInputChange('finishType', e.target.value)}
                  >
                    {Object.entries(surfaceFinishTypes).map(([key, finish]) => (
                      <option key={key} value={key}>
                        {finish.name.ua}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              <div className="form-group">
                <label>URL –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è (–Ω–µ–æ–±–æ–≤'—è–∑–∫–æ–≤–æ)</label>
                <input
                  type="text"
                  placeholder="https://example.com/image.jpg –∞–±–æ –∑–∞–ª–∏—à—Ç–µ –ø–æ—Ä–æ–∂–Ω—ñ–º"
                  value={formData.image}
                  onChange={(e) => handleInputChange('image', e.target.value)}
                />
                <small className="form-hint">
                  –ú–æ–∂–Ω–∞ –∑–∞–ª–∏—à–∏—Ç–∏ –ø–æ—Ä–æ–∂–Ω—ñ–º –∞–±–æ –≤–∫–∞–∑–∞—Ç–∏ –ø–æ–≤–Ω–∏–π URL –¥–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
                </small>
              </div>
            </div>

            {/* –†–æ–∑–º—ñ—Ä–∏ */}
            <div className="form-section">
              <h3>–†–æ–∑–º—ñ—Ä–∏</h3>

              <div className="form-group">
                <label>–°—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–∏–π —Ä–æ–∑–º—ñ—Ä</label>
                <select
                  value={formData.size}
                  onChange={(e) => handleInputChange('size', e.target.value)}
                >
                  {Object.entries(standardSizes).map(([key, size]) => (
                    <option key={key} value={key}>
                      {key === 'custom' ? '–ö–∞—Å—Ç–æ–º–Ω–∏–π —Ä–æ–∑–º—ñ—Ä' : `${size.length}√ó${size.width}√ó${size.height} ${size.unit}`}
                    </option>
                  ))}
                </select>
              </div>

              {customDimensions && (
                <div className="form-row">
                  <div className="form-group">
                    <label>–î–æ–≤–∂–∏–Ω–∞ (–º–º)</label>
                    <input
                      type="number"
                      value={formData.dimensions.length}
                      onChange={(e) => handleDimensionChange('length', e.target.value)}
                      min="1"
                    />
                  </div>
                  <div className="form-group">
                    <label>–®–∏—Ä–∏–Ω–∞ (–º–º)</label>
                    <input
                      type="number"
                      value={formData.dimensions.width}
                      onChange={(e) => handleDimensionChange('width', e.target.value)}
                      min="1"
                    />
                  </div>
                  <div className="form-group">
                    <label>–í–∏—Å–æ—Ç–∞ (–º–º)</label>
                    <input
                      type="number"
                      value={formData.dimensions.height}
                      onChange={(e) => handleDimensionChange('height', e.target.value)}
                      min="1"
                    />
                  </div>
                </div>
              )}
            </div>

            {/* –¶—ñ–Ω–∞ */}
            <div className="form-section">
              <h3>–¶—ñ–Ω–∞</h3>
              <div className="form-row">
                <div className="form-group">
                  <label>–¶—ñ–Ω–∞ (UAH) *</label>
                  <input
                    type="text"
                    placeholder="1140 –≥—Ä–Ω/–º¬≤"
                    value={formData.price.ua}
                    onChange={(e) => handleInputChange('price', e.target.value, 'ua')}
                    required
                  />
                </div>
                <div className="form-group">
                  <label>–¶—ñ–Ω–∞ (EUR)</label>
                  <input
                    type="text"
                    placeholder="21 ‚Ç¨/m¬≤"
                    value={formData.price.en}
                    onChange={(e) => handleInputChange('price', e.target.value, 'en')}
                  />
                </div>
              </div>
            </div>

            {/* –û—Å–æ–±–ª–∏–≤–æ—Å—Ç—ñ */}
            <div className="form-section">
              <h3>–û—Å–æ–±–ª–∏–≤–æ—Å—Ç—ñ –ø—Ä–æ–¥—É–∫—Ç—É</h3>

              <div className="features-list">
                {formData.features.ua.map((feature, index) => (
                  <div key={index} className="feature-item">
                    <span>{feature}</span>
                    <button
                      type="button"
                      onClick={() => removeFeature(index)}
                      className="remove-feature-btn"
                    >
                      ‚úï
                    </button>
                  </div>
                ))}
              </div>

              <div className="add-feature">
                <div className="lang-inputs">
                  <input
                    type="text"
                    placeholder="–û—Å–æ–±–ª–∏–≤—ñ—Å—Ç—å —É–∫—Ä–∞—ó–Ω—Å—å–∫–æ—é"
                    value={newFeature.ua}
                    onChange={(e) => setNewFeature(prev => ({...prev, ua: e.target.value}))}
                  />
                  <input
                    type="text"
                    placeholder="English"
                    value={newFeature.en}
                    onChange={(e) => setNewFeature(prev => ({...prev, en: e.target.value}))}
                  />
                </div>
                <button type="button" onClick={addFeature} className="add-feature-btn">
                  ‚ûï –î–æ–¥–∞—Ç–∏ –æ—Å–æ–±–ª–∏–≤—ñ—Å—Ç—å
                </button>
              </div>
            </div>

            {/* –û–ø—Ü—ñ—ó */}
            <div className="form-section">
              <h3>–û–ø—Ü—ñ—ó</h3>
              <div className="checkbox-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={formData.inStock}
                    onChange={(e) => handleInputChange('inStock', e.target.checked)}
                  />
                  –í –Ω–∞—è–≤–Ω–æ—Å—Ç—ñ
                </label>
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={formData.customizable}
                    onChange={(e) => handleInputChange('customizable', e.target.checked)}
                  />
                  –ú–æ–∂–ª–∏–≤–µ –∫–∞—Å—Ç–æ–º–Ω–µ –≤–∏–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω—è
                </label>
              </div>
            </div>
          </div>

          {error && (
            <div className="form-error">
              ‚ùå {error}
            </div>
          )}

          <div className="form-actions">
            <button
              type="button"
              onClick={closeEditModal}
              className="btn btn-secondary"
              disabled={isLoading}
            >
              –°–∫–∞—Å—É–≤–∞—Ç–∏
            </button>
            <button
              type="submit"
              className="btn btn-primary"
              disabled={isLoading}
            >
              {isLoading ? '–ó–±–µ—Ä–µ–∂–µ–Ω–Ω—è...' : editingProduct ? '–û–Ω–æ–≤–∏—Ç–∏' : '–°—Ç–≤–æ—Ä–∏—Ç–∏'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

export default ProductEditor