import { create } from 'zustand'
import { productsData } from '../constants/productsData'

// API –±–∞–∑–æ–≤–∏–π URL
const API_BASE_URL = process.env.NODE_ENV === 'production'
  ? '/api'
  : 'http://localhost:3001/api'

// API —Ñ—É–Ω–∫—Ü—ñ—ó
const apiClient = {
  async get(endpoint) {
    const response = await fetch(`${API_BASE_URL}${endpoint}`)
    if (!response.ok) throw new Error(`HTTP ${response.status}`)
    return response.json()
  },

  async post(endpoint, data) {
    const response = await fetch(`${API_BASE_URL}${endpoint}`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
    })
    if (!response.ok) throw new Error(`HTTP ${response.status}`)
    return response.json()
  },

  async put(endpoint, data) {
    const response = await fetch(`${API_BASE_URL}${endpoint}`, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data)
    })
    if (!response.ok) throw new Error(`HTTP ${response.status}`)
    return response.json()
  },

  async delete(endpoint) {
    const response = await fetch(`${API_BASE_URL}${endpoint}`, {
      method: 'DELETE'
    })
    if (!response.ok) throw new Error(`HTTP ${response.status}`)
    return response.json()
  }
}

const useAdminProductsStore = create((set, get) => ({
  // –°—Ç–∞–Ω –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  products: [],
  originalProducts: productsData.samples || [],
  hasUnsavedChanges: false,
  isInitialized: false,

  // –°—Ç–∞–Ω UI
  isLoading: false,
  error: null,
  editingProduct: null,
  isEditModalOpen: false,
  searchQuery: '',
  selectedCategory: 'all',
  sortBy: 'name',
  sortOrder: 'asc',

  // –§—ñ–ª—å—Ç—Ä–∞—Ü—ñ—è —Ç–∞ –ø–æ—à—É–∫
  setSearchQuery: (query) => {
    set({ searchQuery: query })
  },

  setSelectedCategory: (category) => {
    set({ selectedCategory: category })
  },

  setSortBy: (field) => {
    const { sortBy, sortOrder } = get()
    const newSortOrder = sortBy === field && sortOrder === 'asc' ? 'desc' : 'asc'
    set({ sortBy: field, sortOrder: newSortOrder })
  },

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –≤—ñ–¥—Ñ—ñ–ª—å—Ç—Ä–æ–≤–∞–Ω–∏—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  getFilteredProducts: () => {
    const { products, searchQuery, selectedCategory, sortBy, sortOrder } = get()

    let filtered = [...products]

    // –ü–æ—à—É–∫
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      filtered = filtered.filter(product =>
        product.name.ua.toLowerCase().includes(query) ||
        product.name.en.toLowerCase().includes(query) ||
        product.id.toLowerCase().includes(query) ||
        product.textureId.toLowerCase().includes(query)
      )
    }

    // –§—ñ–ª—å—Ç—Ä–∞—Ü—ñ—è –ø–æ –∫–∞—Ç–µ–≥–æ—Ä—ñ—è–º
    if (selectedCategory !== 'all') {
      filtered = filtered.filter(product => {
        switch (selectedCategory) {
          case 'gabbro':
            return product.textureId.includes('black')
          case 'granite':
            return product.textureId.includes('red-brown') || product.textureId.includes('gray')
          case 'green':
            return product.textureId.includes('green')
          case 'labradorite':
            return product.textureId.includes('labradorite')
          default:
            return true
        }
      })
    }

    // –°–æ—Ä—Ç—É–≤–∞–Ω–Ω—è
    filtered.sort((a, b) => {
      let aValue, bValue

      switch (sortBy) {
        case 'name':
          aValue = a.name.ua.toLowerCase()
          bValue = b.name.ua.toLowerCase()
          break
        case 'price':
          aValue = parseFloat(a.price.ua.replace(/[^\d]/g, ''))
          bValue = parseFloat(b.price.ua.replace(/[^\d]/g, ''))
          break
        case 'dimensions':
          aValue = a.dimensions.length * a.dimensions.width * a.dimensions.height
          bValue = b.dimensions.length * b.dimensions.width * b.dimensions.height
          break
        case 'inStock':
          aValue = a.inStock ? 1 : 0
          bValue = b.inStock ? 1 : 0
          break
        default:
          aValue = a.id
          bValue = b.id
      }

      if (sortOrder === 'asc') {
        return aValue < bValue ? -1 : aValue > bValue ? 1 : 0
      } else {
        return aValue > bValue ? -1 : aValue < bValue ? 1 : 0
      }
    })

    return filtered
  },

  // –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑ —Å–µ—Ä–≤–µ—Ä–∞ (–∑–∞–≤–∂–¥–∏ —Å–≤—ñ–∂—ñ –¥–∞–Ω—ñ)
  loadProducts: async (forceRefresh = false) => {
    const { isInitialized } = get()

    // –ó–∞–≤–∂–¥–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂—É—î–º–æ —Å–≤—ñ–∂—ñ –¥–∞–Ω—ñ –∞–±–æ –ø—Ä–∏ –ø—Ä–∏–º—É—Å–æ–≤–æ–º—É –æ–Ω–æ–≤–ª–µ–Ω–Ω—ñ
    if (!forceRefresh && isInitialized) {
      console.log('üîÑ –ü—Ä–æ–¥—É–∫—Ç–∏ –≤–∂–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—ñ, –ø—Ä–æ–ø—É—Å–∫–∞—î–º–æ...')
      return { success: true, data: get().products }
    }

    set({ isLoading: true, error: null })

    try {
      console.log('üîÑ –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Å–≤—ñ–∂–∏—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑ API:', `${API_BASE_URL}/products`)
      const response = await apiClient.get('/products')
      console.log('‚úÖ –û—Ç—Ä–∏–º–∞–Ω–æ –ø—Ä–æ–¥—É–∫—Ç–∏:', response.data.length, '—à—Ç')

      set({
        products: response.data,
        isLoading: false,
        hasUnsavedChanges: false,
        isInitialized: true
      })
      return { success: true, data: response.data }
    } catch (error) {
      console.error('‚ùå –ü–æ–º–∏–ª–∫–∞ API:', error.message)
      console.log('üîÑ Fallback –¥–æ –ª–æ–∫–∞–ª—å–Ω–∏—Ö –¥–∞–Ω–∏—Ö...')

      // Fallback –¥–æ –ª–æ–∫–∞–ª—å–Ω–∏—Ö –¥–∞–Ω–∏—Ö –ø—Ä–∏ –ø–æ–º–∏–ª—Ü—ñ
      const { originalProducts } = get()
      console.log('üì¶ –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–æ –ª–æ–∫–∞–ª—å–Ω–∏—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤:', originalProducts.length, '—à—Ç')

      set({
        products: originalProducts,
        error: `–ü–æ–º–∏–ª–∫–∞ –∑'—î–¥–Ω–∞–Ω–Ω—è –∑ API. –í–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—é—Ç—å—Å—è –ª–æ–∫–∞–ª—å–Ω—ñ –¥–∞–Ω—ñ.`,
        isLoading: false,
        isInitialized: true
      })
      return { success: false, error: error.message }
    }
  },

  // –ü—Ä–∏–º—É—Å–æ–≤–µ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  refreshProducts: async () => {
    console.log('üîÑ –ü—Ä–∏–º—É—Å–æ–≤–µ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤...')
    return get().loadProducts(true)
  },

  // CRUD –æ–ø–µ—Ä–∞—Ü—ñ—ó
  addProduct: async (productData) => {
    set({ isLoading: true, error: null })

    try {
      const response = await apiClient.post('/products', productData)

      const { products } = get()
      const updatedProducts = [...products, response.data]

      set({
        products: updatedProducts,
        isLoading: false,
        isEditModalOpen: false,
        editingProduct: null,
        hasUnsavedChanges: true
      })

      return { success: true, product: response.data }
    } catch (error) {
      set({ error: error.message, isLoading: false })
      return { success: false, error: error.message }
    }
  },

  updateProduct: async (productId, updates) => {
    set({ isLoading: true, error: null })

    try {
      const response = await apiClient.put(`/products/${productId}`, updates)

      const { products } = get()
      const updatedProducts = products.map(product =>
        product.id === productId
          ? response.data
          : product
      )

      set({
        products: updatedProducts,
        isLoading: false,
        isEditModalOpen: false,
        editingProduct: null,
        hasUnsavedChanges: true
      })

      return { success: true }
    } catch (error) {
      set({ error: error.message, isLoading: false })
      return { success: false, error: error.message }
    }
  },

  deleteProduct: async (productId) => {
    set({ isLoading: true, error: null })

    try {
      await apiClient.delete(`/products/${productId}`)

      const { products } = get()
      const updatedProducts = products.filter(product => product.id !== productId)

      set({
        products: updatedProducts,
        isLoading: false,
        hasUnsavedChanges: true
      })

      return { success: true }
    } catch (error) {
      set({ error: error.message, isLoading: false })
      return { success: false, error: error.message }
    }
  },

  duplicateProduct: (productId) => {
    set({ isLoading: true, error: null })

    try {
      const { products } = get()
      const originalProduct = products.find(p => p.id === productId)

      if (!originalProduct) {
        throw new Error('–ü—Ä–æ–¥—É–∫—Ç –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ')
      }

      const duplicatedProduct = {
        ...originalProduct,
        id: `${originalProduct.id}-copy-${Date.now()}`,
        name: {
          ua: `${originalProduct.name.ua} (–∫–æ–ø—ñ—è)`,
          en: `${originalProduct.name.en} (copy)`,
          de: `${originalProduct.name.de} (Kopie)`,
          pl: `${originalProduct.name.pl} (kopia)`
        }
      }

      const updatedProducts = [...products, duplicatedProduct]

      set({
        products: updatedProducts,
        isLoading: false
      })

      return { success: true, product: duplicatedProduct }
    } catch (error) {
      set({ error: error.message, isLoading: false })
      return { success: false, error: error.message }
    }
  },

  // –£–ø—Ä–∞–≤–ª—ñ–Ω–Ω—è –º–æ–¥–∞–ª—å–Ω–∏–º –≤—ñ–∫–Ω–æ–º
  openEditModal: (product = null) => {
    set({
      editingProduct: product,
      isEditModalOpen: true,
      error: null
    })
  },

  closeEditModal: () => {
    set({
      editingProduct: null,
      isEditModalOpen: false,
      error: null
    })
  },

  // –û–Ω–æ–≤–ª–µ–Ω–Ω—è —Å—Ç–∞—Ç—É—Å—É –Ω–∞—è–≤–Ω–æ—Å—Ç—ñ
  toggleProductStock: (productId) => {
    const { products } = get()
    const updatedProducts = products.map(product =>
      product.id === productId
        ? { ...product, inStock: !product.inStock }
        : product
    )

    set({ products: updatedProducts })
    return { success: true }
  },

  // –ó–±–µ—Ä–µ–∂–µ–Ω–Ω—è –∑–º—ñ–Ω
  saveProducts: async () => {
    set({ isLoading: true, error: null })

    try {
      const response = await apiClient.post('/products/save')
      set({
        isLoading: false,
        hasUnsavedChanges: false
      })
      return { success: true, message: response.message }
    } catch (error) {
      set({ error: error.message, isLoading: false })
      return { success: false, error: error.message }
    }
  },

  // –°–∫–∏–¥–∞–Ω–Ω—è –∑–º—ñ–Ω
  resetProducts: async () => {
    set({ isLoading: true, error: null })

    try {
      const response = await apiClient.post('/products/reset')
      set({
        products: response.data || get().originalProducts,
        isLoading: false,
        hasUnsavedChanges: false,
        error: null,
        editingProduct: null,
        isEditModalOpen: false
      })
      return { success: true, message: response.message }
    } catch (error) {
      // Fallback –¥–æ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å–∫–∏–¥–∞–Ω–Ω—è
      const { originalProducts } = get()
      set({
        products: [...originalProducts],
        error: `–ü–æ–º–∏–ª–∫–∞ —Å–∫–∏–¥–∞–Ω–Ω—è: ${error.message}. –°–∫–∏–Ω—É—Ç–æ –¥–æ –ª–æ–∫–∞–ª—å–Ω–∏—Ö –¥–∞–Ω–∏—Ö.`,
        editingProduct: null,
        isEditModalOpen: false,
        isLoading: false
      })
      return { success: false, error: error.message }
    }
  },

  // –ï–∫—Å–ø–æ—Ä—Ç/–Ü–º–ø–æ—Ä—Ç –¥–∞–Ω–∏—Ö
  exportProducts: () => {
    const { products } = get()
    const dataStr = JSON.stringify(products, null, 2)
    const dataBlob = new Blob([dataStr], { type: 'application/json' })
    const url = URL.createObjectURL(dataBlob)

    const link = document.createElement('a')
    link.href = url
    link.download = `products-export-${new Date().toISOString().split('T')[0]}.json`
    link.click()

    URL.revokeObjectURL(url)
  },

  // –û—á–∏—Å—Ç–∫–∞ –ø–æ–º–∏–ª–æ–∫
  clearError: () => {
    set({ error: null })
  },

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏
  getProductsStats: () => {
    const { products } = get()

    return {
      total: products.length,
      inStock: products.filter(p => p.inStock).length,
      outOfStock: products.filter(p => !p.inStock).length,
      categories: {
        gabbro: products.filter(p => p.textureId.includes('black')).length,
        granite: products.filter(p =>
          p.textureId.includes('red-brown') || p.textureId.includes('gray')
        ).length,
        green: products.filter(p => p.textureId.includes('green')).length,
        labradorite: products.filter(p => p.textureId.includes('labradorite')).length
      },
      priceRange: {
        min: Math.min(...products.map(p => parseFloat(p.price.ua.replace(/[^\d]/g, '')))),
        max: Math.max(...products.map(p => parseFloat(p.price.ua.replace(/[^\d]/g, ''))))
      }
    }
  }
}))

export default useAdminProductsStore